
import { defineComponent, ref, reactive, PropType } from "vue";
import { AboutContentType } from "@/interface/aboutContent";

export default defineComponent({
  name: "AboutContent",
  props: {
    aboutContent: {
      type: Object as PropType<AboutContentType>,
      required: true,
    },
    isHistoryShow: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  setup(props, { emit }) {
    const clickContent = () => {
      emit("clickContent");
    };
    return { clickContent };
  },
});
